
.div_back {
  position: absolute;
  top: 0;
  left: 20px;
  width: 40px;
  height: 40px;
  color: #fff;
  text-align: center;
  line-height: 42px;
  font-size: 20px;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.div_back:hover {
  background: rgba(0,0,0,0.3);
}
.me_content {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  box-sizing: border-box;
  opacity: 1;
  -webkit-transition: all 0.5s 0.3s;
  transition: all 0.5s 0.3s;
  padding: 30px;
}
.me_content .title {
  font-size: 20px;
  color: #fff;
}
.me_content .personal_tips,
.me_content .technology_tips {
  font-size: 0;
  width: 100%;
  height: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.me_content .personal_tips .tip_list,
.me_content .technology_tips .tip_list {
  display: inline-block;
  height: 30px;
  line-height: 30px;
  padding: 2px 18px;
  font-size: 14px;
  color: #fff;
  position: relative;
  margin: 20px;
  border-radius: 17px;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.me_content .personal_tips .tip_list:hover,
.me_content .technology_tips .tip_list:hover {
  -webkit-box-shadow: 1px 1px 10px 0 rgba(255,255,255,0.24);
          box-shadow: 1px 1px 10px 0 rgba(255,255,255,0.24);
  color: rgba(225,225,225,0.8);
}
.me_content .personal_tips .tip_list .bg-content,
.me_content .technology_tips .tip_list .bg-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 17px;
  z-index: -1;
}
.me_content .personal_tips .tip_list .name,
.me_content .technology_tips .tip_list .name {
  margin-right: 6px;
}


