
@import '~common/response.css';
.g-content {
  position: fixed;
  top: 100px;
  bottom: 50px;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: scroll;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
}
@media screen and (max-width: 1440px) {
.g-content {
    top: 60px;
    bottom: 30px;
}
}
@media screen and (max-width: 992px) {
.g-content {
    top: 40px;
    bottom: 10px;
}
}
::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
::-webkit-scrollbar-track,
::-webkit-scrollbar-thumb {
  border-radius: 999px;
  border: 5px solid transparent;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: 1px 1px 5px rgba(0,0,0,0.2) inset;
          box-shadow: 1px 1px 5px rgba(0,0,0,0.2) inset;
}
::-webkit-scrollbar-thumb {
  min-height: 20px;
  background-clip: content-box;
  -webkit-box-shadow: 0 0 0 5px rgba(255,255,255,0.6) inset;
          box-shadow: 0 0 0 5px rgba(255,255,255,0.6) inset;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
.weather {
  width: auto;
  height: auto;
}
.weather .weather-content {
  border-radius: 4px;
  position: absolute;
  height: auto;
  right: 0;
  width: 260px;
  overflow: hidden;
  padding: 15px;
}
.weather .weather-content.fade-up-enter-to {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.weather .weather-content.fade-up-leave-to {
  -webkit-transition: all 0.2s 0.1s;
  transition: all 0.2s 0.1s;
}
.weather .weather-content.fade-up-enter,
.weather .weather-content.fade-up-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(0, 50px, 0);
          transform: translate3d(0, 50px, 0);
}
.weather .weather-content .weather-tips {
  font-size: 12px;
  color: rgba(225,225,225,0.8);
  display: inline-block;
  text-align: center;
  width: 100%;
}
.weather .weather-content .bg-info {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
.weather .weather-content .weather-detail {
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.weather .weather-content .weather-detail .weather-today {
  width: 100%;
  height: auto;
  padding-bottom: 5px;
  border-bottom: 1px solid rgba(255,255,255,0.1);
}
.weather .weather-content .weather-detail .weather-today .top-today {
  width: 100%;
  height: 50px;
  font-size: 0;
}
.weather .weather-content .weather-detail .weather-today .top-today .left-info {
  display: inline-block;
  width: 100px;
  font-size: 28px;
}
.weather .weather-content .weather-detail .weather-today .top-today .right-info {
  display: inline-block;
  width: auto;
  font-size: 14px;
}
.weather .weather-content .weather-detail .weather-today span {
  display: block;
  margin: 0;
  padding: 4px 0;
  font-size: 12px;
  height: auto;
  line-height: 1.5;
}
.weather .weather-content .weather-detail .weather-other {
  width: 100%;
  overflow: hidden;
  height: 40px;
  border-bottom: 1px dashed rgba(255,255,255,0.1);
  padding: 4px 0;
  font-size: 0;
}
.weather .weather-content .weather-detail .weather-other .weather-wendu,
.weather .weather-content .weather-detail .weather-other .weather-type,
.weather .weather-content .weather-detail .weather-other .weather-time {
  display: inline-block;
  height: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 12px;
}
.weather .weather-content .weather-detail .weather-other .weather-wendu {
  width: 40%;
}
.weather .weather-content .weather-detail .weather-other .weather-time {
  width: 20%;
}
.weather .weather-content .weather-detail .weather-other .weather-type {
  width: 40%;
}


