
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.3);
    transform: scaleY(0.3);
}
}
.video_download {
  max-width: 768px;
  margin: 0 auto;
  overflow-y: auto;
  overflow-x: hidden;
}
.video_download .empty {
  margin: 20px auto;
  color: #fff;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 14px;
}
.video_download .resultlists {
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
  color: #fff;
}
.video_download .resultlists .name {
  color: #fff;
}
.video_download .resultlists .downloadtip {
  font-size: 14px;
}
.video_download .resultlists .resultlist {
  width: 100%;
  height: auto;
  padding: 15px 0;
  border-bottom: 1px solid rgba(255,255,255,0.3);
}
.video_download .resultlists .resultlist .itemlists .itemlist {
  color: #fff;
  border-bottom: 1px solid rgba(255,255,255,0.1);
  word-break: break-word;
}
.video_download .resultlists .resultlist .itemlists .itemlist p {
  margin: 0;
  padding: 2px 0;
}
.video_download .resultlists .resultlist .itemlists .itemlist p.itemname {
  font-size: 16px;
}
.video_download .resultlists .resultlist .itemlists .itemlist p.itemdisc {
  font-size: 14px;
  color: rgba(225,225,225,0.8);
  padding-left: 10px;
}
.video_download .resultlists .resultlist .itemlists .itemlist a {
  text-decoration: underline;
  padding-left: 10px;
  color: #91aace;
  font-size: 14px;
}


