
@import '~common/response.css';
.g-content {
  position: fixed;
  top: 100px;
  bottom: 50px;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: scroll;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
}
@media screen and (max-width: 1440px) {
.g-content {
    top: 60px;
    bottom: 30px;
}
}
@media screen and (max-width: 992px) {
.g-content {
    top: 40px;
    bottom: 10px;
}
}
::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
::-webkit-scrollbar-track,
::-webkit-scrollbar-thumb {
  border-radius: 999px;
  border: 5px solid transparent;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: 1px 1px 5px rgba(0,0,0,0.2) inset;
          box-shadow: 1px 1px 5px rgba(0,0,0,0.2) inset;
}
::-webkit-scrollbar-thumb {
  min-height: 20px;
  background-clip: content-box;
  -webkit-box-shadow: 0 0 0 5px rgba(255,255,255,0.6) inset;
          box-shadow: 0 0 0 5px rgba(255,255,255,0.6) inset;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
.loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #2a323b;
  z-index: 1111;
}
.loader .loader-content {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  right: 0;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
}
.loader .loader-content .loader-range {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-animation: rotate 0.8s linear infinite;
          animation: rotate 0.8s linear infinite;
  border-radius: 100%;
  border-top: 1px solid #545a6a;
  border-bottom: 1px solid #d4d4db;
  border-left: 1px solid #545a6a;
  border-right: 1px solid #d4d4db;
  opacity: 0.5;
  -webkit-animation-name: load_cycle;
          animation-name: load_cycle;
}
@-webkit-keyframes load_cycle {
0% {
    -webkit-transform: rotateZ(-360deg);
            transform: rotateZ(-360deg);
}
100% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
}
}
@-webkit-keyframes load_cycle {
0% {
    -webkit-transform: rotateZ(-360deg);
            transform: rotateZ(-360deg);
}
100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
}
}
@keyframes load_cycle {
0% {
    -webkit-transform: rotateZ(-360deg);
            transform: rotateZ(-360deg);
}
100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
}
}


