
.switch {
  width: 38px;
  height: 20px;
  border-radius: 10px;
  cursor: pointer;
}
.switch .range {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #fff;
  display: inline-block;
  margin-left: 1px;
  margin-top: 1px;
  vertical-align: top;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
}


