
.updatetips {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 11;
  font-weight: 500;
  display: none;
}
.updatetips .updatetime {
  position: absolute;
  bottom: 30px;
  right: 30px;
  color: rgba(255,255,255,0.6);
  font-size: 12px;
}
.updatetips .update_mask {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(12,14,27,0.96);
}
.updatetips .update_mask.fade-enter-to,
.updatetips .update_mask.fade-leave-to {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.updatetips .update_mask.fade-enter,
.updatetips .update_mask.fade-leave-to {
  opacity: 0;
}
.updatetips .updatetips_info {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  top: 45%;
  left: 50%;
  bottom: 0;
  right: 0;
  width: 100%;
  max-width: 414px;
  padding: 0 30px;
  height: auto;
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
  border-radius: 4px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.updatetips .updatetips_info.scale-enter-to,
.updatetips .updatetips_info.scale-leave-to {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.updatetips .updatetips_info.scale-enter {
  -webkit-transform: translate(-50%, -30%);
          transform: translate(-50%, -30%);
  opacity: 0;
}
.updatetips .updatetips_info.scale-leave-to {
  opacity: 0;
}
.updatetips .updatetips_info .img_bg {
  width: 100%;
  height: auto;
  min-height: 70px;
  position: relative;
  border-radius: 4px 4px 0 0;
}
.updatetips .updatetips_info .img_bg .title {
  color: rgba(255,255,255,0.9);
  padding: 15px;
  text-align: center;
  margin: 0;
}
.updatetips .updatetips_info .img_bg img {
  width: 100%;
  height: auto;
  border-radius: 4px;
}
.updatetips .updatetips_info .img_bg .update_version {
  float: right;
  color: rgba(255,255,255,0.9);
  font-size: 12px;
  font-weight: 600;
}
.updatetips .updatetips_info .content {
  width: 100%;
  height: auto;
  padding: 15px 0;
  line-height: 1.8;
  font-size: 14px;
  color: rgba(255,255,255,0.9);
  font-weight: 400;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.updatetips .updatetips_info .update_select {
  width: 100%;
  height: 36px;
  margin: 0 auto;
  margin-top: 30px;
  font-size: 0;
  border-radius: 0;
  border: 1px solid rgba(255,12,12,0.03);
  background: rgba(255,12,12,0.05);
}
.updatetips .updatetips_info .update_select input {
  width: 100%;
  height: 100%;
  font-size: 14px;
  height: 36px;
  line-height: 36px;
  border: 0 none;
  outline: 0 none;
  cursor: pointer;
  -webkit-appearance: none;
  outline: none;
  background: transparent;
}
.updatetips .updatetips_info .update_select input.giveup_update {
  border-radius: 0 0 0 4px;
  color: rgba(255,255,255,0.6);
}
.updatetips .updatetips_info .update_select input.go_update {
  border-radius: 0 0 4px 0;
  color: rgba(255,255,255,0.6);
}
.updatetips .updatetips_info .update_select input:hover {
  color: rgba(255,255,255,0.8);
  background: rgba(255,12,12,0.08);
}


