
.colorpicker {
  position: relative;
  width: 80%;
  max-width: 720px;
  height: auto;
  margin: 0 auto;
  font-size: 0;
  overflow: auto;
}
.colorpicker .colorlist {
  display: inline-block;
  width: 100%;
  height: auto;
  margin: 0 auto;
  line-height: 10px;
}
.colorpicker .colorlist .color-detail {
  width: 20px;
  height: 20px;
  display: inline-block;
  border-radius: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
}
.colorpicker .colorlist .color-detail:hover {
  border: 1px solid #fff;
}


