
.suggest position:fixed {
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  border-radius: 12px;
}
.div_back {
  position: absolute;
  top: 0;
  left: 15px;
  width: 40px;
  height: 40px;
  color: #fff;
  text-align: center;
  line-height: 42px;
  font-size: 20px;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.div_back:hover {
  background: rgba(0,0,0,0.3);
}
.suggest_content {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  box-sizing: border-box;
  opacity: 1;
  -webkit-transition: all 0.5s 0.3s;
  transition: all 0.5s 0.3s;
}
.suggest_content .type_name {
  color: #fff;
  margin: 0;
}
.suggest_content .suggest_detail {
  display: block;
  margin: 0 auto;
  width: 400px;
  max-width: 100%;
}
.suggest_content .suggest_detail .title {
  color: #fff;
}
.suggest_content .suggest_area {
  background: transparent;
  color: #fff;
  resize: none;
  width: 100%;
  display: block;
  margin: 0 auto;
  outline: none;
  padding: 10px;
  border: 1px solid #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 1.5;
}
.suggest_content .submit_suggest {
  display: block;
  width: 100%;
  height: 50px;
  line-height: 50px;
  margin-top: 30px;
  color: #fff;
  font-size: 16px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #fff;
  cursor: pointer;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}
.suggest_content .submit_suggest:hover {
  background: rgba(255,255,255,0.4);
}
.suggest_content .read_suggest {
  color: #fff;
  cursor: pointer;
  text-align: center;
}
.suggest_content .read_suggest:hover {
  color: #ccc;
  text-decoration: underline;
}
.suggest_lists {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: #fff;
}
.suggest_lists .lists_c {
  width: 100%;
  max-width: 400px;
  position: absolute;
  top: 50px;
  left: 50%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
}
.suggest_lists .lists_c .title {
  padding: 25px 0;
  display: inline-block;
}
.suggest_lists .lists_c li .date {
  font-size: 12px;
  margin-left: 10px;
  color: #f00;
}


