
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.3);
    transform: scaleY(0.3);
}
}
.audio_download {
  max-width: 768px;
  margin: 0 auto;
  overflow-y: auto;
  overflow-x: hidden;
}
.audio_download .empty {
  margin: 20px auto;
  color: #fff;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 14px;
}
.audio_download .resultlists {
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
}
.audio_download .resultlists .name {
  color: #fff;
}
.audio_download .resultlists .downloadtip {
  font-size: 14px;
  color: #fff;
}
.audio_download .resultlists .resultlist {
  width: 100%;
  height: auto;
  padding: 15px 0;
}
.audio_download .resultlists .resultlist .list {
  width: 100%;
  height: auto;
  font-size: 0;
  padding: 5px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid rgba(255,255,255,0.1);
}
.audio_download .resultlists .resultlist .list .singerimg {
  width: 50px;
  display: inline-block;
  vertical-align: middle;
  border: none 0;
}
.audio_download .resultlists .resultlist .list .name {
  display: inline-block;
  color: #fff;
  font-size: 14px;
  width: calc(100% - 201px);
  vertical-align: middle;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 10px;
}
.audio_download .resultlists .resultlist .list .download {
  display: inline-block;
  width: 150px;
  height: 50px;
  line-height: 50px;
  vertical-align: middle;
  font-size: 0;
  cursor: pointer;
  color: rgba(225,225,225,0.8);
}
.audio_download .resultlists .resultlist .list .download .bz,
.audio_download .resultlists .resultlist .list .download .hi,
.audio_download .resultlists .resultlist .list .download .no {
  display: inline-block;
  height: auto;
  font-size: 12px;
  padding: 2px 5px;
  cursor: pointer;
  margin-right: 10px;
  height: 20px;
  vertical-align: middle;
  line-height: 20px;
}
.audio_download .resultlists .resultlist .list .download .bz {
  color: rgba(225,225,225,0.8);
  border: 1px solid rgba(225,225,225,0.8);
}
.audio_download .resultlists .resultlist .list .download .hi {
  color: rgba(225,225,225,0.8);
  border: 1px solid rgba(225,225,225,0.8);
}
.audio_download .resultlists .resultlist .list .download .no {
  color: rgba(225,225,225,0.8);
  border: 1px solid rgba(225,225,225,0.8);
}


