
::-webkit-input-placeholder {
  color: rgba(225,225,225,0.8);
}
:-moz-placeholder {
  color: rgba(225,225,225,0.8);
}
:-ms-input-placeholder {
  color: rgba(225,225,225,0.8);
}
.resources position:fixed {
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  border-radius: 12px;
}
.div_back {
  position: absolute;
  top: 0;
  left: 15px;
  width: 40px;
  height: 40px;
  color: #fff;
  text-align: center;
  line-height: 42px;
  font-size: 20px;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.div_back:hover {
  background: rgba(0,0,0,0.3);
}
.resources_content {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  box-sizing: border-box;
  opacity: 1;
  -webkit-transition: all 0.5s 0.3s;
  transition: all 0.5s 0.3s;
}
.resources_content .serachArea {
  position: absolute;
  top: 0;
  height: 50px;
  padding: 7px 0;
  width: 100%;
  left: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.resources_content .serachArea .selectContent {
  width: 70%;
  min-width: 300px;
  max-width: 500px;
  margin: 0 auto;
  height: 36px;
  font-size: 0;
  color: #fff;
  position: relative;
}
.resources_content .serachArea .selectContent .searchInfo {
  display: inline-block;
  vertical-align: top;
  height: 36px;
  width: calc(100% - 70px);
  font-size: 14px;
  padding: 0;
  text-indent: 5px;
  color: #fff;
  background: rgba(0,0,0,0.1);
  border-radius: 4px 0 0 4px;
  border: 1px solid #fff;
  border-right: 0 none;
  outline: 0 none;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.resources_content .serachArea .selectContent .serachType {
  position: absolute;
  top: 5px;
  bottom: 5px;
  right: 74px;
  width: 80px;
  background: rgba(0,0,0,0.1);
  outline: none;
  color: #fff;
  padding: 0 2px;
}
.resources_content .serachArea .selectContent .serachType option {
  background: rgba(0,0,0,0.6);
}
.resources_content .serachArea .selectContent button {
  display: inline-block;
  vertical-align: top;
  height: 36px;
  width: 66px;
  font-size: 14px;
  color: #fff;
  border-radius: 0 4px 4px 0;
  border: 1px solid #fff;
  background: rgba(0,0,0,0.1);
  padding: 0;
  outline: 0 none;
  margin: 0;
  cursor: pointer;
}
.resources_content .serachArea .selectContent button:hover {
  background: rgba(0,0,0,0.3);
}
.resources_content .list_resources {
  position: absolute;
  top: 80px;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 15px;
}


