
.pic {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  border-radius: 12px;
}
.pic .pic_bg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  z-index: -1;
}
.pic .pic_content {
  position: fixed;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  box-sizing: border-box;
}
.pic .image_detail {
  position: fixed;
  top: 100px;
  bottom: 50px;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0) scale(1);
          transform: translate3d(-50%, 0, 0) scale(1);
  -webkit-transform-origin: center center;
          transform-origin: center center;
}
.pic .image_detail.fade-scale-enter-active,
.pic .image_detail.fade-scale-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.pic .image_detail.fade-scale-enter,
.pic .image_detail.fade-scale-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(-50%, 0, 0) scale(0);
          transform: translate3d(-50%, 0, 0) scale(0);
}
.pic .image_detail .swiper-container {
  height: 100%;
  overflow: hidden;
}
.pic .image_detail .swiper-container .swiper-wrapper {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pic .image_detail .swiper-container .swiper-wrapper .swiper-slide {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pic .image_detail .swiper-container .swiper-wrapper .swiper-slide img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  background: #000;
}
.pic .image_detail .swiper-container .swiper-pagination,
.pic .image_detail .swiper-container .swiper-pagination-bullet {
  margin: 0 4px;
}
.pic .image_detail .swiper-container .swiper-pagination.swiper-pagination-bullet-active,
.pic .image_detail .swiper-container .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #f00 !important;
}
.pic .image_detail .bg_info {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.pic .image_detail .colse_image_detail {
  position: absolute;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
  top: 0;
  right: 0;
  z-index: 2;
  cursor: pointer;
}
.pic .image_detail .colse_image_detail i {
  font-size: 46px;
  color: #fff;
  vertical-align: middle;
}
.pic .image_detail .colse_image_detail .bg_colse_image_detail {
  width: 100%;
  height: 100%;
  z-index: -1;
}
.pic .image_detail .set_Bg {
  color: #fff;
  position: absolute;
  z-index: 1;
  bottom: 10px;
  right: 10px;
  font-size: 12px;
  padding: 5px 10px;
  background: rgba(0,0,0,0.1);
  cursor: pointer;
}
.pic .image_detail .set_Bg:hover {
  background: rgba(0,0,0,0.4);
}
.pic .image_detail .bg_image_info {
  display: block;
  width: 100%;
  height: 100%;
}
.pic .image_detail .image_info {
  height: 100%;
  display: block;
  margin: 0 auto;
}


