
.picList_content .picList {
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.picList_content .picList .title {
  font-size: 36px;
  font-weight: 400;
  color: #fff;
  margin: 15px 0;
}
.picList_content .picList .detail {
  font-size: 12px;
  color: #fff;
}
.picList_content .picList span {
  margin-right: 10px;
}
.picList_content .picList .disc {
  display: inline-block;
  font-size: 16px;
  color: #fff;
  margin: 0;
  padding: 10px 0;
  line-height: 1.5;
  min-width: 50%;
  border-bottom: 1px solid rgba(255,255,255,0.1);
}
.picList_content .picList .div_image {
  width: 100%;
  height: auto;
  font-size: 0;
  padding: 20px 0;
}
.picList_content .picList .image {
  display: inline-block;
  margin-top: 20px;
  width: 110px;
  height: 110px;
  cursor: pointer;
  border-radius: 4px;
  margin: 5px;
}
@media screen and (max-width: 768px) {
.picList_content .picList .image.image {
    width: 90px;
    height: 90px;
}
.picList_content .picList .image.image:first-child {
    margin-left: 0;
}
}
@media screen and (min-width: 768px) {
.picList_content .picList .image.image {
    width: 150px;
    height: 150px;
    margin: 10px;
}
.picList_content .picList .image.image:first-child {
    margin-left: 0;
}
}
.picList_content .image_detail {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.picList_content .image_detail.fade-scale-enter-active,
.picList_content .image_detail.fade-scale-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.picList_content .image_detail.fade-scale-enter,
.picList_content .image_detail.fade-scale-leave-to {
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.picList_content .image_detail .swiper-container {
  height: 100%;
}
.picList_content .image_detail .swiper-container .swiper-wrapper .swiper-slide {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.picList_content .image_detail .swiper-container .swiper-pagination,
.picList_content .image_detail .swiper-container .swiper-pagination-bullet {
  margin: 0 4px;
}
.picList_content .image_detail .swiper-container .swiper-pagination.swiper-pagination-bullet-active,
.picList_content .image_detail .swiper-container .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #f00 !important;
}
.picList_content .image_detail .bg_info {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.picList_content .image_detail .colse_image_detail {
  position: absolute;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
  top: 0;
  right: 0;
  z-index: 2;
  cursor: pointer;
}
.picList_content .image_detail .colse_image_detail i {
  font-size: 46px;
  color: #fff;
  vertical-align: middle;
}
.picList_content .image_detail .colse_image_detail .bg_colse_image_detail {
  width: 100%;
  height: 100%;
  z-index: -1;
}
.picList_content .image_detail .bg_image_info {
  display: block;
  width: 100%;
  height: 100%;
}
.picList_content .image_detail .image_info {
  height: 100%;
  display: block;
  margin: 0 auto;
}


