::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
::-webkit-scrollbar-track,
::-webkit-scrollbar-thumb {
  border-radius: 999px;
  border: 5px solid transparent;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: 1px 1px 5px rgba(0,0,0,0.2) inset;
          box-shadow: 1px 1px 5px rgba(0,0,0,0.2) inset;
}
::-webkit-scrollbar-thumb {
  min-height: 20px;
  background-clip: content-box;
  -webkit-box-shadow: 0 0 0 5px rgba(255,255,255,0.6) inset;
          box-shadow: 0 0 0 5px rgba(255,255,255,0.6) inset;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.3);
    transform: scaleY(0.3);
}
}
.setting {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  border-radius: 12px;
}
.setting .set_bg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  z-index: -1;
}
.setting .setting-warpper {
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
}
.setting .setting-warpper .title {
  color: #fff;
  margin: 0;
}
.setting .setting-warpper .set_list {
  padding: 10px 0;
  color: #fff;
  font-size: 0;
}
.setting .setting-warpper .set_list.border-1px {
  position: relative;
}
.setting .setting-warpper .set_list.border-1px:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(255,255,255,0.3);
  content: ' ';
}
.setting .setting-warpper .set_list .name {
  display: inline-block;
  font-size: 14px;
  vertical-align: middle;
  width: calc(100% - 150px);
}
.setting .setting-warpper .set_list .name.center {
  font-size: 14px;
  text-align: center;
  width: 100%;
