}
.setting .setting-warpper .set_list .name.cursor {
  cursor: pointer;
}
.setting .setting-warpper .set_list .name span {
  cursor: pointer;
}
.setting .setting-warpper .set_list .name span:hover {
  text-decoration: underline;
}
.setting .setting-warpper .set_list .switch-right {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
  float: right;
}
.setting .setting-warpper .set_list .switch-right .title {
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  margin-right: 10px;
  color: #ccc;
  min-width: 50px;
  text-align: center;
}
.setting .setting-warpper .set_list .switch-right .switch {
  display: inline-block;
