}
.setting .setting-warpper .set_list .switch-right .inputText {
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  margin-right: 10px;
  color: #666;
  min-width: 50px;
  text-align: center;
  outline: none;
  width: 120px;
  height: 32px;
  margin: 0;
  border: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.setting .selectBg {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 8px;
  z-index: 1;
  background: rgba(0,0,0,0.3);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.setting .selectBg.fade-enter-to,
.setting .selectBg.fade-leave-to {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.setting .selectBg.fade-enter,
.setting .selectBg.fade-leave-to {
  opacity: 0;
}
.setting .version {
  position: absolute;
  bottom: 0;
  right: 30px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 12px;
}


