
.news {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  border-radius: 12px;
}
.news .div_back {
  position: absolute;
  top: 0;
  left: 15px;
  width: 40px;
  height: 40px;
  color: #fff;
  text-align: center;
  line-height: 42px;
  font-size: 20px;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.news .div_back:hover {
  background: rgba(0,0,0,0.3);
}
.news .news_content {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  box-sizing: border-box;
  opacity: 1;
  -webkit-transition: all 0.5s 0.3s;
  transition: all 0.5s 0.3s;
}
.news .news_content .selectNewsType {
  position: fixed;
  top: 15px;
  left: 55px;
  right: 0;
  height: 36px;
  line-height: 36px;
  overflow-x: hidden;
}
.news .news_content .selectNewsType .content_type {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: 100%;
  overflow-y: hidden;
}
.news .news_content .selectNewsType .content_type .news_type_list {
  color: #fff;
  font-weight: 500;
  margin-right: 20px;
  float: left;
  cursor: pointer;
}
.news .news_content .selectNewsType .content_type .news_type_list.active {
  color: #f00;
}
.news .news_content .newsLists {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: calc(50% + 25px);
  max-width: calc(1240px - 50px);
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px 5px;
  box-sizing: border-box;
  opacity: 1;
  -webkit-transition: all 0.5s 0.3s;
  transition: all 0.5s 0.3s;
}
.news .news_content .newsLists a {
  display: block;
  border-bottom: 1px solid rgba(255,255,255,0.1);
  padding: 10px;
}
.news .news_content .newsLists a:hover {
  background: rgba(0,0,0,0.2);
}
.news .news_content .newsLists a .newstitle {
  margin: 5px 0;
  line-height: 1.5;
}
.news .news_content .newsLists a .image_lists img {
  width: 150px;
  margin-right: 10px;
}
.news .news_content .newsLists a .tips {
  margin-right: 10px;
  font-size: 14px;
}
@media screen and (max-width: 525px) {
.news .news_content {
    padding: 5px 0;
}
.news .news_content .selectNewsType {
    left: 15px;
    top: 0;
}
.news .news_content .selectNewsType .content_type .news_type_list {
    margin-right: 10px;
}
.news .news_content .newsLists {
    left: 50%;
    max-width: 1240px;
    width: 100%;
    padding: 0;
}
.news .news_content .newsLists a .image_lists {
    width: 100%;
    font-size: 0;
}
.news .news_content .newsLists a .image_lists img {
    display: inline-block;
    width: 30%;
    margin: 1.5%;
}
}


