
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.3);
    transform: scaleY(0.3);
}
}
.music_toplist .toplist_title {
  font-size: 18px;
  color: #fff;
}
.music_toplist .toplist_content {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: table;
}
.music_toplist .toplist_content .toplist_l {
  padding: 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.music_toplist .toplist_content .toplist_l .toplist_detail {
  height: auto;
  cursor: pointer;
  position: relative;
}
.music_toplist .toplist_content .toplist_l .toplist_detail:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../../../static/coverall.png");
  background-position: 0 0;
  background-size: 272%;
}
.music_toplist .toplist_content .toplist_l .toplist_detail:before {
  content: attr(title);
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 30px;
  line-height: 30px;
  text-overflow: ellipsis;
  padding: 0 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  white-space: nowrap;
  overflow: hidden;
  background: rgba(0,0,0,0.3);
  color: #fff;
  font-size: 12px;
}
.music_toplist .toplist_content .toplist_l .toplist_detail img {
  display: block;
  width: 100%;
  height: auto;
}
.music_toplist .toplist_content .toplist_l .toplist_detail .bg_list {
  display: block;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
@media screen and (max-width: 1550px) {
.music_toplist .toplist_content .toplist_l {
    padding: 15px;
}
}
@media screen and (max-width: 768px) {
.music_toplist .toplist_content .toplist_l {
    padding: 10px;
}
}


