
@import '~common/response.css';
.g-content {
  position: fixed;
  top: 100px;
  bottom: 50px;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: scroll;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
}
@media screen and (max-width: 1440px) {
.g-content {
    top: 60px;
    bottom: 30px;
}
}
@media screen and (max-width: 992px) {
.g-content {
    top: 40px;
    bottom: 10px;
}
}
::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
::-webkit-scrollbar-track,
::-webkit-scrollbar-thumb {
  border-radius: 999px;
  border: 5px solid transparent;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: 1px 1px 5px rgba(0,0,0,0.2) inset;
          box-shadow: 1px 1px 5px rgba(0,0,0,0.2) inset;
}
::-webkit-scrollbar-thumb {
  min-height: 20px;
  background-clip: content-box;
  -webkit-box-shadow: 0 0 0 5px rgba(255,255,255,0.6) inset;
          box-shadow: 0 0 0 5px rgba(255,255,255,0.6) inset;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
.search {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 11;
}
.search .bg_search {
  background: rgba(0,0,0,0.8);
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  opacity: 1;
}
.search .bg_search.fade-enter-to,
.search .bg_search.fade-leave-to {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.search .bg_search.fade-enter,
.search .bg_search.fade-leave-to {
  opacity: 0;
}
.search .search_content {
  width: 500px;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
  margin-top: 30px;
}
.search .search_content.silde-top-enter-to,
.search .search_content.silde-top-leave-to {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.search .search_content.silde-top-enter,
.search .search_content.silde-top-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(0, -50px, 0);
          transform: translate3d(0, -50px, 0);
}
.search .search_content .search_input {
  display: block;
  width: 100%;
  height: 40px;
  text-indent: 4px;
  font-size: 14px;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: transparent;
  color: rgba(225,225,225,0.8);
  border: 1px solid rgba(225,225,225,0.8);
  outline: none;
}
.search .search_content .search-btn {
  position: absolute;
  right: 0;
  top: 0;
  width: 60px;
  height: 100%;
  color: #fff;
  border-left: 1px solid rgba(225,225,225,0.8);
  text-align: center;
  cursor: pointer;
}
.search .search_select {
  width: 500px;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
  margin-top: 50px;
  font-size: 0;
  height: calc(100% - 140px);
  overflow: auto;
}
.search .search_select.silde-bottom-enter-to,
.search .search_select.silde-bottom-leave-to {
  -webkit-transition: all 0.8s 0.2s;
  transition: all 0.8s 0.2s;
}
.search .search_select.silde-bottom-enter,
.search .search_select.silde-bottom-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(0, 50px, 0);
          transform: translate3d(0, 50px, 0);
}
.search .search_select .select_type {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}
.search .search_select .select_type .select_title {
  width: 100%;
  height: 50px;
  line-height: 50px;
  margin: 0;
  font-size: 18px;
  color: #fff;
  text-indent: 5px;
  border-bottom: 1px solid rgba(255,255,255,0.1);
  margin-bottom: 10px;
}
.search .search_select .select_type .select_span {
  display: inline-block;
  width: auto;
  padding: 2px 10px;
  height: 20px;
  line-height: 20px;
  border-radius: 2px;
  cursor: pointer;
  margin: 15px 8px;
  font-size: 14px;
  color: rgba(225,225,225,0.8);
  border: 1px solid rgba(225,225,225,0.8);
}
.search .search_select .select_type .select_span:hover {
  color: #fff;
  border: 1px solid #fff;
}


