
.selectmask {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 8px;
  z-index: 1;
  background: rgba(12,14,27,0.96);
}
.selectmask.fade-enter-to,
.selectmask.fade-leave-to {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.selectmask.fade-enter,
.selectmask.fade-leave-to {
  opacity: 0;
}
.selectmask .list_content {
  position: fixed;
  top: 80px;
  bottom: 50px;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  box-sizing: border-box;
  opacity: 1;
  -webkit-transition: all 0.5s 0.3s;
  transition: all 0.5s 0.3s;
}
.selectmask .list_content .selectmask_list {
  padding: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.selectmask .list_content .selectmask_list .mask_list_info {
  position: relative;
  outline: 5px solid rgba(255,255,255,0.1);
  cursor: pointer;
  width: 90%;
  margin: 0 auto;
}
.selectmask .list_content .selectmask_list .mask_list_info:hover {
  outline-color: rgba(255,255,255,0.3);
}
.selectmask .list_content .selectmask_list .mask_list_info.active {
  outline-color: rgba(36,174,61,0.4);
}
.selectmask .list_content .selectmask_list .mask_list_info.active:hover {
  outline-color: rgba(36,174,61,0.6);
}
.selectmask .list_content .selectmask_list .mask_list_info .title {
  color: #fff;
  text-align: center;
  display: block;
  font-size: 14px;
}
.selectmask .list_content .selectmask_list .mask_list_info .demo_info {
  width: 100%;
  position: relative;
}
.selectmask .list_content .selectmask_list .mask_list_info .demo_info .demo_img {
  width: 100%;
  display: block;
  outline: none 0;
}
.selectmask .list_content .selectmask_list .mask_list_info .demo_info .demo_bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}


