
.home {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.home .pic_bg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  z-index: -1;
}
.home .home_center {
  max-width: 1240px;
  border-radius: 12px;
  backgoround: #fff;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.home .home_center .home_content {
  padding: 0 20px 50px 20px;
  position: relative;
}
.home .home_center .home_content .title {
  font-size: 22px;
  color: #fff;
}
.home .home_center .home_content .title span {
  font-size: 14px;
  vertical-align: bottom;
  font-weight: normal;
  margin-left: 20px;
}
.home .home_center .home_content .title span.playpause {
  background: #fff;
  display: inline-block;
  color: #f55021;
  width: auto;
  vertical-align: middle;
  height: 24px;
  font-weight: 600;
  font-size: 12px;
  line-height: 24px;
  padding: 0 8px;
  border-radius: 4px;
  cursor: pointer;
}
.home .home_center .home_content .title span.playpause i {
  vertical-align: middle;
  display: inline-table;
  padding-bottom: 2px;
}
.home .home_center .home_content .disc {
  font-size: 14px;
  color: #fff;
}
.home .home_center .home_set {
  position: absolute;
  right: 30px;
  bottom: 60px;
  color: #fff;
  width: auto;
  font-size: 0;
}
.home .home_center .home_set .set_list {
  display: inline-block;
  width: 40px;
  height: 40px;
  font-size: 16px;
  vertical-align: top;
  border: 2px solid #fff;
  border-radius: 50%;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-left: 25px;
  cursor: pointer;
}
.home .home_center .home_set .set_list i {
  color: #fff;
  vertical-align: baseline;
}
.home .home_center .home_set .set_list.disabled {
  cursor: default;
  border: 2px solid rgba(233,233,233,0.46);
}
.home .home_center .home_set .set_list.disabled i {
  color: rgba(233,233,233,0.46);
}
.home .home_center .tips {
  position: absolute;
  right: 30px;
  left: 30px;
  bottom: 0;
  color: #fff;
  height: 40px;
  line-height: 40px;
  font-size: 10px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: right;
}
.home .home_center .tips a {
  color: #fff;
  margin-left: 10px;
  text-decoration: none;
}
@media screen and (min-width: 992px) {
.home .home_center .home_content {
    padding: 0 80px 50px 80px;
    position: relative;
}
}


