
.div_back {
  position: absolute;
  top: 0;
  left: 20px;
  width: 40px;
  height: 40px;
  color: #fff;
  text-align: center;
  line-height: 42px;
  font-size: 20px;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.div_back:hover {
  background: rgba(0,0,0,0.3);
}
.href_right {
  height: 60px;
  color: #fff;
  text-align: center;
  line-height: 60px;
  float: right;
}
.joke_content {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  box-sizing: border-box;
  opacity: 1;
  -webkit-transition: all 0.5s 0.3s;
  transition: all 0.5s 0.3s;
}


