
.works position:fixed {
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  right: 0;
  border-radius: 12px;
}
.div_back {
  position: absolute;
  top: 0;
  left: 15px;
  width: 40px;
  height: 40px;
  color: #fff;
  text-align: center;
  line-height: 42px;
  font-size: 20px;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.div_back:hover {
  background: rgba(0,0,0,0.3);
}
.updateinfo_content {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 50%;
  max-width: 1240px;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  margin: 0 auto;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  padding: 15px;
  box-sizing: border-box;
  opacity: 1;
  -webkit-transition: all 0.5s 0.3s;
  transition: all 0.5s 0.3s;
}
.updateinfo_content .updatelist {
  width: 100%;
  padding: 0 15px;
  height: 50px;
  line-height: 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  color: rgba(225,225,225,0.8);
  font-size: 0;
  cursor: pointer;
  border-bottom: 1px solid rgba(255,255,255,0.1);
}
.updateinfo_content .updatelist span {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.updateinfo_content .updatelist span.index {
  display: inline-block;
  width: 50px;
  text-align: center;
  font-size: 14px;
}
.updateinfo_content .updatelist span.updatetime {
  display: inline-block;
  width: calc(30% - 50px);
  text-align: left;
  font-size: 14px;
}
.updateinfo_content .updatelist span.updatecontent {
  display: inline-block;
  font-size: 14px;
  width: 45%;
  text-align: left;
}
.updateinfo_content .updatelist span.updateversion {
  display: inline-block;
  font-size: 14px;
  width: 25%;
  text-align: right;
}


